@media (min-width: 768px) {
  .navbar-nav {
    float: left;
    margin: 0; } }

.blog-post {
  color: black;
  border: 3px solid #000000;
  border-radius: 10px;
  -moz-border-radius: 15px;
  transition: all 0.4s ease-in-out;
  a {
    color: black; } }

.blog-post-title {
  color: black;
  background-color: white; }

.blog-message > p {
  font-size: 16px;
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif; }

body {
  background: url("/bg/proxy.jpg"); }

.blokkok {
  margin-top: 10px;
  margin-left: 4%;
  height: auto;
  width: 90%;
  font-size: 15px;
  padding: 20px;
  background: 20px;
  background: rgba(204, 204, 204, 0.6);
  border: 3px solid #aaaaaa;
  border-radius: 10px;
  -moz-border-radius: 15px;
  transition: all 0.4s ease-in-out;
  > {}
  .container {
    color: black;
    img {
      display: block;
      margin: 0 auto; } } }

.lostablos {
  font-size: 18px;
  text-align: center;
  li {
    display: inline-block;
    margin: 0 40px;
    &:hover {
      color: aqua; } }
  img {
    margin-left: 30px;
    align-items: center;
    width: 200px;
    height: 200px; }
  h1 {
    font-family: clara;
    font-size: 30px;
    color: gold;
    margin-top: 30px;
    margin-bottom: 30px; } }

.ff {
  color: white; }

.nav {
  width: 55%;
  margin: 0 auto;
  /*display: flex; */
  /*font: ; */
  /*flex-direction: row-reverse; */
  font-size: 15px;
  font-weight: 300;
  letter-spacing: 0.5px;
  > li {
    &:hover > a {
      text-decoration: none;
      background-color: #695ca1; }
    &.active {
      > a {
        background-color: #7a0c31; }
      &:hover > a {
        color: white;
        background-color: #5a1043; } }
    > a {
      background-color: white;
      color: black; } } }
